@tailwind base;
@tailwind components;
@tailwind utilities;


@import url('https://fonts.googleapis.com/css2?family=Raleway&display=swap');

@layer base{
    body{
        @apply font-[Raleway]
    }
    li{
        @apply cursor-pointer
    }
    .card {
            background: linear-gradient(135deg, rgba(106, 8, 8, 0.8) 25%, rgba(31, 253, 149, 0) 25%, rgba(66, 14, 197, 0) 50%, rgba(106, 8, 8, 0.8) 25%, rgba(31, 253, 149, 0) 25%, rgba(66, 14, 197, 0) 50%,);
            border: 1px solid rgba( 231, 84, 128, 10);
            border-radius: 18px; /* Increase the border-radius for a smoother appearance */
            padding: 20px;
            /*animation: bounce 5s infinite;*/
            box-shadow: 0px 8px 12px rgba(0, 0, 0, 0.8); /* Adjust the shadow properties */
          }
          

      }
      
      @keyframes bounce {
        0%, 20%, 50%, 80%, 100% {
          transform: translateY(0);
        }
        40% {
          transform: translateY(-20px);
        }
        60% {
          transform: translateY(-10px);
        }
      }




  
  .slide-in-animation{
    transition: left 0.5s ease-in-out;

  }

  @keyframes wave {
    0%, 100% {
      transform: translateY(0);
    }
    25% {
      transform: translateY(-10px);
    }
    50% {
      transform: translateY(0);
    }
    75% {
      transform: translateY(10px);
    }
  }
  
  .wave-animation:nth-child(1) {
    animation: wave 5s infinite;
    animation-delay: 0s;
  }
  
  .wave-animation:nth-child(2) {
    animation: wave 5s infinite;
    animation-delay: 0.5s;
  }
  
  .wave-animation:nth-child(3) {
    animation: wave 5s infinite;
    animation-delay: 1s;
  }
  
  /* Projects.css */
.project-card {
  animation: running-box-shadow;
}

@keyframes running-box-shadow {
  0% {
    box-shadow: 0 0 20px rgba(255, 0, 0, 0.5);
  }
  25% {
    box-shadow: 0 0 20px rgba(0, 255, 0, 0.5);
  }
  50% {
    box-shadow: 0 0 20px rgba(0, 0, 255, 0.5);
  }
  75% {
    box-shadow: 0 0 20px rgba(255, 255, 0, 0.5);
  }
  100% {
    box-shadow: 0 0 20px rgba(255, 0, 255, 0.5);
  }
}


/* Skills.css */
@keyframes scaleSmall {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(0.8);
  }
  100% {
    transform: scale(1);
  }
}

@keyframes scaleBig {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1);
  }
}


.icon-container img:nth-child(even) {
  animation: scaleSmall 4s alternate infinite;
}

.icon-container img:nth-child(odd) {
  animation: scaleBig 4s alternate infinite;
}
.icon-container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap; /* Allow icons to wrap to the next line if needed */
}

.icon-container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  gap: 20px; /* Add gap between icons */
}

.icon-container img {
  width: 50px;
  height: 50px;
  object-fit: contain;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s, box-shadow 0.3s;
  animation: hoverAnimation 1s infinite alternate; /* Apply the animation */
}

/* Add animation for hovering */
@keyframes hoverAnimation {
  0% {
    transform: scale(1); /* Original size */
  }
  100% {
    transform: scale(1.2); /* Larger size */
  }
}

/* Add box shadow on hover */
.icon-container img:hover {
  box-shadow: 0px 8px 12px rgba(0, 0, 0, 0.2);
}


